

@font-face{/*font*/
	font-family: carbon;
	src: url('font/carbon.ttf');
}
h3{
    width: 100%;
}

.social_media{
    display: flex;
    flex-direction: column;
}

.logo_container{
    display: flex;
    flex-direction: row;
    justify-content: center;    
    
}

.logo{
    display: flex;
    width: 5%;
    display: flex;
    -webkit-transition: all 0.5s;
    z-index: 2;
    margin:1%
}

.logo:hover{
    /* position: relative; */
    display: flex;
    cursor: pointer;
    transform: scale(1.25);
    
}

.pp_container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 17%;
}


.ppic{
    width: 12%;
    height: 8em;
    filter: brightness(80%);
    border-radius: 50%;
    margin: 10px solid;
    z-index: 2;
    opacity: 1;
    -webkit-transition: all 0.5s;
}

.ppic:hover
{
    display: flex;
    transform: scale(1.12);
}


.ppic_click_open{
    position: absolute;
    display: flex;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    top: 67%;
    margin-top:6.5%;
    left: 0;
    right: 0%;
	background-color: #202013;
    color: white;
    height:120%;
    z-index: 1;     
    border-radius: 0.5em;

}

.hr1{
    /* position: absolute; */
    width: 42%;
    /* margin-top: 7.5%; */
    margin-right:2%
}

.hr2 {
    width:42%; 
    /* margin-top: 7.5%;  */
